}
.goodsMenuWrap .current{
		background-color: #fff;
		color: red;
}
.goodsMenuWrap .current::after{
		display: none;
}
.supportsBrand{		
	    display: inline-block;
	    width: 14px;
	    height: 14px;
	    background-size: 100%;
	    vertical-align: top;
	    margin-right: 4px;
}
.goodsMenuWrap li::after{
		content: '';
		display: block;
		position: absolute;
		height: 1px;
		width: 56px;
		bottom: 0;
		left: 50%;
		margin-left: -28px;
		transform: scaleY(0.5);
		background-color: rgba(7,17,27,0.1);
}
