
.seller{
	position: absolute;
	left: 0;
	top: 177px;
	bottom: 0;
	width: 100%;
	overflow: hidden;
	background-color: #f3f5f7;
}
.sellerContent{
	position: relative;
}
.sellerContent .overView{
	margin-bottom: 16px;
	position: relative;
	padding: 18px;
	background-color: #fff;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.sellerContent .overView .brefInfo{
	position: relative;
	padding-bottom: 18px;
	font-size: 0;
}
.sellerContent .overView .brefInfo::after{
	content: '';
	display: block;
	position: absolute;
	left: 0;
	bottom: 0;
	height: 1px;
	width: 100%;
	background-color: rgba(7,17,27,0.1);
	transform: scaleY(0.5);
}
.sellerContent .overView .sellerName{
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
	margin-bottom: 8px;
}
.sellerContent .overView .stars{
	display: inline-block;
	padding-top: 1px;
	height: 18px;
}
.sellerContent .overView .ratingCount,.sellerContent .overView .sellCount{
	margin-left: 8px;
	line-height: 18px;
	font-size: 10px;
	color: rgb(77,85,93);
	vertical-align: top;
}
.sellerContent .overView .sellCount{
	margin-left: 12px;
}
.sellerContent .overView .delivInfo{
	display: -ms-flexbox;
	display: flex;
	margin-top: 18px;
}
.sellerContent .overView .delivItem{
	-ms-flex: 1;
	    flex: 1;
	position: relative;
	text-align: center;
}
.sellerContent .overView .delivItem::after{
	content: '';
	position: absolute;
	right: 0;
	top: 0;
	height: 100%;
	width: 1px;
	background-color: rgba(7,17,27,0.1);
	transform: scaleX(0.5);
}
.sellerContent .overView .delivItem:last-child::after{
	display: none;
}
.sellerContent .overView .delivInfoText{
	display: inline-block;
	margin-bottom: 4px;
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.sellerContent .overView .delivInfoCon{
	line-height: 24px;
	font-size: 10px;
	color: rgb(7,17,27);
}
.sellerContent .overView .detailNum{
	font-size: 24px;
}
.sellerContent .overView .collect{
	position: absolute;
	top: 18px;
	right: 18px;
	width: 36px;
	color: #d4d6d9;
	text-align: center;
}
.sellerContent .overView .icon-favorite{
	margin-bottom: 4px;
	line-height: 24px;
	font-size: 24px;
}
.sellerContent .overView div.active{
	color: rgb(240,20,20);
}
.sellerContent .overView .weishoucang,.sellerContent .overView .yishoucang{
	line-height: 10px;
	font-size: 10px;
	color: #d4d6d9;
}
.sellerContent .overView .yishoucang{
	color: rgb(77,85,93);
}
.decrease {
    background-image: url(decrease_4@2x.png);
}
@media (min-device-pixel-ratio:3),
(-webkit-min-device-pixel-ratio:3) {
.decrease {
        background-image: url(decrease_4@3x.png);
}
}
.discount {
    background-image: url(discount_4@2x.png);
}
@media (min-device-pixel-ratio:3),
(-webkit-min-device-pixel-ratio:3) {
.discount {
        background-image: url(discount_4@3x.png);
}
}
.guarantee {
    background-image: url(guarantee_4@2x.png);
}
@media (min-device-pixel-ratio:3),
(-webkit-min-device-pixel-ratio:3) {
.guarantee {
        background-image: url(guarantee_4@3x.png);
}
}
.invoice {
    background-image: url(invoice_4@2x.png);
}
@media (min-device-pixel-ratio:3),
(-webkit-min-device-pixel-ratio:3) {
.invoice {
        background-image: url(invoice_4@3x.png);
}
}
.special {
    background-image: url(special_4@2x.png);
}
@media (min-device-pixel-ratio:3),
(-webkit-min-device-pixel-ratio:3) {
.special {
        background-image: url(special_4@3x.png);
}
}
.sellerContent .bAndS{
	margin-bottom: 16px;
	padding: 18px 18px 0;
	background-color: #fff;
}
.sellerContent .bAndS .title{
	margin-bottom: 8px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.sellerContent .bAndS .bulletin{
	padding-bottom: 16px;
	line-height: 24px;
	font-size: 12px;
	color: rgb(240,20,20);
}
.sellerContent .bAndS .supportsItem{
	position: relative;
	padding: 16px 12px;
	line-height: 16px;
}
.sellerContent .bAndS .supportsItem::after{
	content: '';
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	height: 1px;
	width: 100%;
	background-color: rgba(7,17,27,0.1);
	transform: scaleY(0.5);
}
.sellerContent .bAndS .brand{
	display: inline-block;
	margin-right: 6px;
	height: 16px;
	width: 16px;
	background-size: 100% 100%;
	vertical-align: top;
}
.sellerContent .bAndS .supportDetail{
	font-size: 12px;
	color: rgb(7,17,27);
	vertical-align: top;
}
.sellerContent .pics{
	padding: 18px;
	background-color: #fff;
}
.sellerContent .pics .title{
	margin-bottom: 12px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.sellerContent .pics .picWrap{
	width: 100%;
	overflow: hidden;
	white-space: nowrap;
	font-size: 0;
}
.sellerContent .pics .picItem{
	display: inline-block;
	margin-right: 6px;
	width: 120px;
	height: 90px;
}
.sellerContent .pics .picItem:last-child{
	margin-right: 0;
}
.sellerContent .infos{
	margin-bottom: 16px;
	padding: 18px;
	background-color: #fff;
}
.sellerContent .infos .title{
	margin-bottom: 12px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.sellerContent .infos .infosItem{
	padding: 16px 12px;
	position: relative;
	line-height: 16px;
	font-size: 12px;
	color: rgb(7,17,27);
}
.sellerContent .infos .infosItem::after{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 1px;
	background-color: rgba(7,17,27,0.1);
	transform: scaleY(0.5);
}



