
.ratingSelect .selectType {
    position: relative;
    margin: 0 18px;
    padding-bottom: 18px;
    font-size: 0;
}
.ratingSelect .selectType::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(7,17,27,0.1);
    transform: scaleY(0.5);
}
.ratingSelect .selectType span {
    padding: 8px 12px;
    margin-right: 8px;
    border-radius: 1px;
    line-height: 16px;
    font-size: 12px;
    color: rgb(77, 85, 93);
}
.ratingSelect .selectType .num {
    font-size: 8px;
    margin-left: 4px;
}
.ratingSelect .selectType .all {
    display: inline-block;
    background-color: rgba(0, 160, 220, 0.2);
}
.ratingSelect .selectType .all.active,
.ratingSelect .selectType .positive.active {
    background-color: rgba(0, 160, 220, 1);
}
.ratingSelect .selectType .positive {
    display: inline-block;
    background-color: rgba(0, 160, 220, 0.2);
}
.ratingSelect .selectType .negative {
    display: inline-block;
    background-color: rgba(77, 85, 93, 0.2);
}
.ratingSelect .selectType .negative.active {
    background-color: rgba(77, 85, 93, 1);
}
.ratingSelect .selectType .active {
    color: #fff;
}
.ratingSelect .switch {
    padding: 12px 18px;
    font-size: 0;
    color: rgb(147, 153, 159);
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.ratingSelect .switch i {
    line-height: 24px;
    font-size: 24px;
    margin-right: 4px;
}
.ratingSelect .switch .text {
    line-height: 24px;
    font-size: 12px;
    vertical-align: top;
}
.ratingSelect .switch .on {
    color: rgb(0, 160, 220);
}



