
.goodDetail{
	position: fixed;
	left: 0;
	top: 0;
	bottom: 48px;
	z-index: 80;
	width: 100%;
	background-color: #f3f5f7;
}
.goodDetailShow-enter-active,.goodDetailShow-leave-active{
	transform: translate3d(0,0,0);
	transition: all 0.5s;
}
.goodDetailShow-enter,.goodDetailShow-leave-active{
	transform: translate3d(100%,0,0);
}
.goodDetail .goodAvatar{
	position: relative;
	width: 100%;
	height: 0;
	padding-top: 100%;
}
.goodDetail .goodAvatar img{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.goodDetail .detailBack{
	position: absolute;
	top: 10px;
	left: 0;
}
.goodDetail .icon-arrow_lift{
	display: inline-block;
	padding: 10px;
	font-size: 20px;
	color: #fff;
}
.goodDetail .shopName{
	position: relative;
	padding: 18px;
	background-color: #fff;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.goodDetail .shopName .name{
	line-height: 14px;
	font-size: 14px;
	font-weight: 700;
	color: rgb(7,17,27);
	margin-bottom: 8px;
}
.goodDetail .shopName .sellInfo{
	font-size: 0;
	margin-bottom: 18px;
}
.goodDetail .shopName .sellCount,.goodDetail .shopName .rating{
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.goodDetail .shopName .shopRating{
	margin-left: 12px;
}
.goodDetail .shopName .price{
	font-size: 0;
}
.goodDetail .shopName .rmb,.goodDetail .shopName .priceNow{
	line-height: 24px;
	font-size: 10px;
	color: rgb(240,20,20);
}
.goodDetail .shopName .priceNow{
	font-size: 14px;
	font-weight: 700;
}
.goodDetail .shopName .oldPrice{
	margin-left: 12px;
	line-height: 24px;
	font-size: 10px;
	color: rgb(147,153,159);
	text-decoration: line-through;
}
.goodDetail .shopName .addToCart{
	position: absolute;
	right: 18px;
	bottom: 18px;
}
.goodDetail .shopName .addCart{
	width: 74px;
	height: 24px;
	line-height: 24px;
	text-align: center;
	border-radius: 12px;
	background-color: rgb(0,160,220);
	font-size: 10px;
	color: #fff;
}
.goodDetail .shopName .cartContr{
	margin: 0 -6px -6px 0;
}
.goodDetail .goodInfo{
	margin-top: 16px;
	padding: 18px;
	border-top: 1px solid rgba(7,17,27,0.1);
	border-bottom: 1px solid rgba(7,17,27,0.1);
	background-color: #fff;
}
.goodDetail .goodInfo .title{
	margin-bottom: 6px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.goodDetail .goodInfo p{
	padding: 0 8px;
	line-height: 24px;
	font-size: 12px;
	color: rgb(77,85,93);
}
.goodDetail .rating{
	margin-top: 16px;
	border-top: 1px solid rgba(7,17,27,0.1);
	background-color: #fff;
}
.goodDetail .rating .title{
	margin: 18px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.goodDetail .rating .noRatings{
	height: 90px;
	text-align: center;
	font-size: 10px;
	line-height: 90px;
	color: rgb(77,85,93);
}
.goodDetail .rating .ratingWrap{
	padding: 0 18px;
}
.goodDetail .rating .ratingItem{
	position: relative;
	padding: 16px 0;
}
.goodDetail .rating .ratingItem::after{
	content: '';
	display: block;
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 1px;
	transform: scaleY(0.5);
	-webkit-transform: scaleY(0.5);
	background-color: rgba(7,17,27,0.1);
}
.goodDetail .rating .user{
	position: absolute;
	right: 0;
	top: 16px;
}
.goodDetail .rating .username{
	line-height: 12px;
	font-size: 10px;
	color: rgb(147,153,159);
	margin-right: 6px;
	vertical-align: middle;
}
.goodDetail .rating .useravatar{
	width: 12px;
	height: 12px;
	border-radius: 50%;
	vertical-align: middle;
}
.goodDetail .rating .rateTime{
	line-height: 12px;
	font-size: 10px;
	color: rgb(147,153,159);
	margin-bottom: 6px;
}
.goodDetail .rating .rateContent{
	line-height: 16px;
	font-size: 12px;
	color: rgb(7,17,27);
}
.goodDetail .rating .rateType{
	color: rgb(147,153,159);
	margin-right: 4px;
	vertical-align: middle;
}
.goodDetail .rating .icon-thumb_up{
	color: rgb(0,160,220);
}



