
@media (min-device-pixel-ratio:1.5),
(-webkit-min-device-pixel-ratio:1.5) {
.border-1px::after {
        transform: scaleY(0.7);
}
}
@media (min-device-pixel-ratio:2),
(-webkit-min-device-pixel-ratio:2) {
.border-1px::after {
        transform: scaleY(0.5);
}
}
.border-1px::after {
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    content: '';
}
.tab {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    color: rgb(77, 84, 93);
}
.tab-item {
    -ms-flex: 1;
        flex: 1;
    text-align: center;
}
.tab a {
    display: block;
}
.tab a.active {
    color: rgb(240, 20, 20);
}



