
.shopcart{
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 99;
	width: 100%;
	height: 48px;
}
.shopcart .content{
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	background-color: #141d27;
}
.shopcart .content-left{
	-ms-flex: 1;
	    flex: 1;
	font-size: 0;
}
.shopcart .cart-icon{
	display: inline-block;
	position: relative;
	top: -10px;
	margin: 0 12px;
	padding: 6px;
	width: 44px;
	height: 44px;
	text-align: center;
	vertical-align: top;
	border-radius: 50%;
	background-color: #141d27;
}
.shopcart .cart-icon i{
	display: inline-block;
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: #2b343c;
	font-size: 24px;
	line-height: 44px;
	color: rgba(255,255,255,0.4);
}
.shopcart .cartNotEmpty i{
	background-color: rgb(0,160,220);
	color: #fff;
}
.shopcart .cart-icon .num{
	position: absolute;
	top: 0px;
	right: 0px;
	display: inline-block;
	line-height: 16px;
	height: 16px;
	width: 24px;
	border-radius: 7px;
	background-color: rgb(240,20,20);
	text-align: center;
	font-size: 9px;
	font-weight: 700;
	color: #fff;
	box-shadow: 0 2px 4px 0 rgba(0,0,0,0.4);
}
.shopcart .goods-cost,.shopcart .deliv-cost{
	display: inline-block;
	margin: 12px 0;
	padding-right: 12px;
	line-height: 24px;
	font-size: 16px;
	font-weight: 700;
	color: rgba(255,255,255,0.4);
	border-right: 1px solid rgba(255,255,255,0.1);
}
.shopcart .totalNotEmpty{
	color: #fff;
}
.shopcart .deliv-cost{
	margin: 12px 0 12px 12px;
	font-size: 10px;
	font-weight: normal;		
	border-right: none;
}
.shopcart .content-right{
	-ms-flex: 0 0 105px;
	    flex: 0 0 105px;
	width: 105px;
	height: 100%;
	box-sizing: border-box;
	padding: 12px 8px;
	background-color: #2b343c;
	font-size: 12px;
	font-weight: 700px;
	line-height: 24px;
	color: rgba(255,255,255,0.4);
	text-align: center;
}
.shopcart .uCanPay{
	background-color: #00b43c;
	color: #fff;
}
.shopcart .ballsContainer{
}
.shopcart .ballWrap{
	position: fixed;
	left: 32px;
	bottom: 22px;
	z-index: 99;
}
.shopcart .innerBall{
	height: 16px;
	width: 16px;
	border-radius: 50%;
	background-color: rgb(0,160,220);
}
.shopcartList{
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
	width: 100%;
	transform: translate3d(0,-100%,0);
}
.toggleList-enter-active,.toggleList-leave-active{
	transition: all 0.5s;
}
.toggleList-enter,.toggleList-leave-active{
	transform: translate3d(0,0,0);
}
.shopcartList .listHeader{
	height: 40px;
	padding: 0 18px;
	background-color: #f3f5f7;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.shopcartList .listHeader .title{
	display: inline-block;
	float: left;
	line-height: 40px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.shopcartList .listHeader .emptyCart{
	display: inline-block;
	float: right;
	line-height: 40px;
	font-size: 12px;
	color: rgb(0,160,220);
}
.shopcartList .listContent{
	padding: 0 18px;
	overflow: hidden;
	max-height: 217px;
	background-color: #fff;
}
.shopcartList .listContent .good{
	position: relative;
	box-sizing: border-box;
	height: 48px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	padding: 12px 0;
}
.shopcartList .listContent .name{
	display: inline-block;
	line-height: 24px;
	font-size: 14px;
	color: rgb(7,17,27);
	font-weight: 700;
}
.shopcartList .listContent .price{
	position: absolute;
	right: 90px;
	bottom: 12px;
	line-height: 24px;
	font-size: 14px;
	font-weight: 700;
	color: rgb(240,20,20);
}
.shopcartList .listContent .price span{
	font-size: 10px;
	font-weight: normal;
}
.shopcartList .listContent .cartControlWrap{
	position: absolute;
	right: 0;
	bottom: 5px;
}
.shopcartWrap .listFilter{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 90;
	background-color: rgba(7,17,27,0.6);
	filter: blur(10px);
}
.listFilterShow-enter-active,.listFilterShow-leave-active{
	transition: all 0.5s;	
	opacity: 1;
}
.listFilterShow-enter,.listFilterShow-leave-active{
	opacity: 0;
}



