
.singleGoodWrap{
	padding: 18px;
}
.singleGood{
	position: relative;
	display: -ms-flexbox;
	display: flex;
}
.singleGood::after{
	content: '';
	height: 1px;
	width: 100%;
	position: absolute;
	bottom: -18px;
	left: 0;
	transform: scaleY(0.5);
	background-color: rgba(7,17,27,0.1);
}
.singleGoodWrap:last-child .singleGood::after{
	display: none;
}
.singleGood .avatar{
	width: 57px;
	height: 57px;
	-ms-flex: 0 0 57px;
	    flex: 0 0 57px;
	vertical-align: top;
}
.singleGood .content{
	-ms-flex: 1;
	    flex: 1;
	padding-left: 10px;
}
.singleGood .content .name{
	margin-top: 2px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.singleGood .description,.content .sellInfo{
	margin-top: 8px;
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.singleGood .rating{
	margin-left: 12px;
}
.singleGood .priceInfo{
	font-size: 14px;
	line-height: 14px;
	margin-top: 8px;
}
.singleGood .priceTag{
	line-height: 10px;
	font-size: 10px;
	vertical-align: bottom;
	color: rgb(240,20,20);
}
.singleGood .price{
	font-weight: 700;
	color: rgb(240,20,20);
	vertical-align: bottom;
}
.singleGood .oldPrice{
	margin-left: 8px;
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
	vertical-align: bottom;
	text-decoration: line-through;
}
.singleGood .cartControllWrap{
	position: absolute;
	right: 0;
	bottom: -12px;
}



